html{
  font-size: 10px;
  @media (max-width: 1200px){
    font-size: 9px;
  }
  @media (max-width: 990px){
    font-size: 8px;
  }
  @media (max-width: 768px){
    font-size: 7px;
  }
  @media (max-width: 540px){
    font-size: 6px;
  }
}
$font: 'Montserrat', sans-serif;;
$url: '../img/';
$color-black: #000;
$color-white: #fff;
$color-light: #999;
$color-blue: rgb(138, 204, 255);


body{
  font-family: $font;
  color: $color-black;
  font-weight: 400;
  min-width: 320px;
  overflow-x: hidden;
  *{
    outline: none;
  }
  @media (min-width: 1210px){
    .container {
      width: 1210px;
    }
  }
  .flex-container{
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  /* Animete css */
