  /* Buttons */
  .btn{
    &.btn-border{
      white-space: nowrap;
      border: 1px solid rgba(255, 255, 255, 0.25);
      border-radius: 0;
      outline: none;
      cursor: pointer;
      text-align: center;
      line-height: 13px;
      padding: 15px 50px;
      letter-spacing: 1px;
      font-weight: 500;
      font-size: 17px;
      color: $color-white;
      transition: .3s;
      &:hover{
        border-color: $color-white;
      }
    }
  }

  img{
    max-width: 100%;
  }

  .nav-row{
    height: 80px;
    background-color: $color-white;
    padding-top: 20px;
    .container{
      display: flex;
    }
    .header__top__logo{
      flex: 0 0 180px;
      padding-top: 5px;
      @media (max-width: 768px){
        flex: 1 1 auto;
        img{
          max-width: 150px;
        }
      }
    }
    .header__top__nav{
      flex: 1 1 auto;
      padding-top: 10px;
    }
    .nav{
      text-align: right;
      @media (max-width: 768px){
        position: absolute;
        background: rgba(0, 0, 0, .9);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100vh;
        padding-top: 100px;
        display: none;
        z-index: 999;
        &.active{

        }
      }
      .nav__item{
        display: inline-block;
        @media (max-width: 768px){
          display: block;
          text-align: center;
          padding: 20px 10px;
          user-select: none;
        }
        a{
          font-size: 12px;
          padding: 0 20px;
          color: $color-light;
          text-transform: uppercase;
          @media (max-width: 992px){
            padding: 0 10px;
          }
          @media (max-width: 768px){
            color: $color-white;
            font-size: 14px;
          }
          &:hover{
            text-decoration: none;
            color: $color-blue;
          }
        }
      }
    }
  }
  .menu-icon {
    position: relative;
    width: 35px;
    height: 55px;
    cursor: pointer;
    margin: 0;
    float: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    display: none;
    z-index: 1000;
    @media (max-width: 768px){
      display: block;
    }
    & > span {
      position: absolute;
      top: 50%;
      display: block;
      width: 100%;
      height: 2px;
      margin-top: -0.5em;
      background-color: #999;
      border-radius: 3px;
      &:before, &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #999;
        border-radius: 3px;
        -webkit-transition: all 0.3s;
        transition: all 0.3s;
      }
      &:before {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
      }
      &:after {
        -webkit-transform: translateY(10px);
        transform: translateY(10px);
      }
    }
    &.open {
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      & > span {
        &:before, &:after {
          -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
        }
      }
    }
  }

  .header{
    background-size: cover;
    overflow: hidden;
    color: $color-white;
    .header__top{
      position: fixed;
      z-index: 999;
      top: 0;
      left: 0;
      right: 0;
      transition: .3s;
      &.fixed{
        padding-top: 10px;
        height: 60px;
        opacity: .9;
        &:hover{
          opacity: 1;
        }
      }
    }
    .slider{
      height: 100vh;
      background: url('data:image/jpeg;base64,') center no-repeat;
      background-size: cover;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      padding: 0 10px;
      &:after{
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0, 0, 0, .4);
        z-index: 0;
      }
      .btn{
        margin: 10px auto 0 auto;
        position: relative;
        z-index: 1;
      }
      .slider__header{
        font-size: 13rem;
        line-height: 10rem;
        font-weight: 700;
        color: $color-white;
        position: relative;
        z-index: 1;
        @media (max-width: 500px){
          font-size: 10rem;
        }
      }
      .slider__subtitle{
        font-size: 7rem;
        line-height: 7rem;
        font-weight: 400;
        color: $color-blue;
        position: relative;
        z-index: 1;
      }
    }
  }

  /* section */
  .section__title {
    font-size: 5.8rem;
    line-height: 5.8rem;
    margin-bottom: 20px;
  }
  .section__info{
    p{
      font-size: 15px;
      color: #555;
    }
  }
  .section{
    display: flex;
    min-height: 800px;
    @media (max-width: 768px){
      min-height: 200px;
      flex-wrap: wrap;
    }
    .section__img{
      flex: 1 1 60%;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      @media (max-width: 768px){
        flex: 1 1 100%;
        height: 500px;
        order: 1;
      }
    }
    .section__info{
      flex: 1 1 40%;
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 50px;
      @media (max-width: 768px){
        flex: 1 1 100%;
        order: 2;
        padding-top: 30px;
        margin-bottom: 70px;
      }
    }
    &.section-1{
      .section__img{
        background-image: url('data:image/jpeg;base64,');
      }
    }
    &.section-2{
      .section__img{
        background-image: url('data:image/jpeg;base64,');
      }
    }
    &.section-3{
      .section__img{
        background-image: url('data:image/jpeg;base64,');
        flex: 1 1 65%;
        @media (max-width: 768px){
          flex: 1 1 100%;
          height: 500px;
          order: 1;
        }
      }
      .section__info {
        flex: 1 1 35%;
        @media (max-width: 768px){
          flex: 1 1 100%;
          order: 2;
          padding-top: 30px;
          margin-bottom: 70px;
        }
      }
    }
  }
  .section-icons{
    padding: 20px;
    background: $color-blue;
    &>div{
      display: flex;
      @media (max-width: 440px){
        flex-wrap: wrap;
      }
    }
    .section-icons__item{
      flex: 1 1 auto;
      padding: 20px;
      color: $color-white;
      @media (max-width: 768px){
        flex: 1 1 50%;
        text-align: center;
      }
    }
    .icon{
      display: flex;
      align-items: center;
      .icon__ico{
        flex: 0 0 auto;
        font-size: 45px;
        padding-right: 20px;
        padding-top: 15px;
        @media (max-width: 990px){
          display: none;
        }
      }
      .icon__info{
        flex: 1 1 auto;
        .icon__info__header{
          font-size: 6rem;
          font-weight: 400;
        }
        .icon__info__subtitle{
          font-size: 16px;
          font-weight: 300;
        }
      }
    }
  }
  .section-5{
    display: flex;
    min-height: 600px;
    align-items: center;
    justify-content: center;
    background: url($url + 'section-4.jpeg') center no-repeat;
    background-size: cover;
    text-align: center;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, .3);
    }
    .section__info{
      position: relative;
      z-index: 2;
      .section__title {
        color: $color-white;
      }
      p{
        color: $color-white;
      }
    }
  }
  .section-info{
    padding: 70px 0;
    &>div{
      display: flex;
      flex-wrap: wrap;
    }
    .section-info__item{
      flex: 1 1 320px;
      padding: 20px;
      text-align: center;
      .section-info__item__icon{
        font-size: 65px;
        padding: 20px;
        color: $color-blue;
      }
      .section__title{
        font-size: 2.8rem;
      }
      p{
        font-size: 16px;
        font-weight: 300;
        padding: 0 20px;
      }
    }
  }
  .footer{
    border-top: 1px solid rgba(85, 85, 85, .08);
    &.nav-row{
      .nav{
        text-align: right;
        @media (max-width: 768px){
          display: none;
        }
        .nav__item{
          a{
            text-transform: none;
          }
        }
      }
      .header__top__logo{
        flex: 0 0 100px;
        padding-top: 15px;
      }
    }
  }

}